html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.container_background {
  background: linear-gradient(255deg, rgb(0, 50, 73) 10%, rgb(1, 18, 46) 50%, rgb(0, 1, 14) 100%);
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  max-width: 35rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container-sm {
    max-width: 50rem;
  }
}
@media (min-width: 1025px) {
  .container-sm {
    max-width: 90rem;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 4.4rem;
  text-align: center;
  margin: 5rem 0 2rem;
  color: #383D3B;
}
@media (min-width: 768px) {
  h1 {
    font-size: 7.2rem;
    margin: 0 0 5rem;
    padding-top: 20rem;
  }
}

h2 {
  font-size: 4rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 5rem;
  }
}

h3 {
  font-size: 3rem;
}
@media (min-width: 768px) {
  h3 {
    font-size: 4rem;
  }
}

h4 {
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  h4 {
    font-size: 3rem;
  }
}

p {
  font-size: 1.4rem;
  color: #6b7280;
  line-height: 1.8;
}
@media (min-width: 768px) {
  p {
    font-size: 2rem;
  }
}

h3, h4, p, li {
  color: #9d9d9d;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  color: #000;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 6rem;
  margin-top: 0;
}
@media (min-width: 768px) {
  .tagline {
    font-size: 3rem;
  }
}

.remove {
  display: none;
}

.page_description {
  text-align: center;
  margin: 0;
  font-size: 2.2rem;
  color: #6b7280;
  font-weight: 400;
}
h3 {
  margin-top: 20rem;
  text-transform: uppercase;
  margin-bottom: 5rem;
}

.hero {
  background-image: linear-gradient(rgb(0, 0, 0), rgba(32, 32, 32, 0.4)), url(../../build/img/bgimg.png);
  background-size: cover;
  background-position: center center;
  height: 100vh;
  max-width: 100%;
  align-content: end;
}
.hero .hero_content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.hero .hero_content h1 {
  text-align: start;
  padding: 0 2rem;
  font-size: 8rem;
  color: #FFFFFF;
  font-weight: 700;
  max-width: 100%;
  text-transform: uppercase;
  display: flex;
  gap: 1rem;
}
@media (min-width: 768px) {
  .hero .hero_content h1 {
    font-size: 12rem;
  }
}
.hero .hero_content h1 {
  white-space: nowrap;
  overflow: hidden;
}
.hero .hero_content h1 span {
  gap: 1rem;
  display: inline-block;
  animation: moveLeft 30s linear infinite;
}
@keyframes moveLeft {
  0% {
    transform: translateX(4%);
  }
  100% {
    transform: translateX(-100%);
  }
  @media (min-width: 768px) {
    0% {
      transform: translateX(10%);
    }
    100% {
      transform: translateX(-100%);
    }
  }
}