// Fontos
$font_headings: 'Inter', sans-serif;
$font_texts: 'Poppins', sans-serif;

// Media Queries Sizes
$mobile: 480px;
$tablet: 768px;
$desktop: 1025px;

// Colors
$darkblue: #091f32;
$turquoise: #219ebc;
$lightblue: #8ECAE6;
$orange:#FB8500;
$yellow: #F59E0B;
$lightgold: #FAD961;
$gold: #AB9144;
$darkgold: #92792f;
$darkGrey: #383D3B;
$darkBlueGrey: #30363d;

$bluegradient: linear-gradient(255deg, rgb(0, 50, 73) 10%, rgb(1, 18, 46) 50%, rgb(0, 1, 14) 100%);
$goldgradient: linear-gradient(0deg, $lightgold 0%, $gold 35%, $darkgold 70%);


$grey: #6b7280;
$lightGrey: #9d9d9d;
$greyBlue: #E8F0FE;
$white: #FFFFFF;

$greenBrand: #709317;
$green: #436949;
$orangeFlame: #E4572E;

$black: #000;
$red: rgb(222, 51, 51);
$greenLight: rgb(111, 155, 111);

// Font Weights
$light: 200;
$regular: 400;
$semibold: 600;
$bold: 700;
$blackF: 900;


$separation: 5rem;