@use 'base/variables' as v;

/** Media Queries **/
@mixin mobile {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin button($bgColor, $textColor, $borderColor) {
    background-color: $bgColor;
    padding: 1.5rem 3rem;
    color: $textColor;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: v.$regular;
    border: solid .2rem $borderColor;
    border-radius: .5rem;
    transition: all .3s ease;
    text-align: center;
    display: block;
    width: 100%;
    letter-spacing: 0.1rem;
    @include tablet {
        width: 30rem;
        font-size: 2rem;
    }
    &:hover {
        transform: scale(1.05);
        color: v.$gold;
        border: solid .2rem v.$gold;
        box-shadow: 0 0 1.5rem -.6rem v.$white;
        cursor: pointer;
    }
}

@mixin grid($columns, $space ) {
    display: grid;
    gap: $space;
    grid-template-columns: repeat($columns, 1fr);
}

@mixin textGradient($color, $color2) {
    background: v.$goldgradient;
    color: transparent;
    background-clip: text;
    text-align: center;
    text-transform: uppercase;
    font-size: 6rem;


    
    @include tablet {
        font-size: 10rem;
        
        &.resize {
            width: 26rem;
            text-align: center;
            font-size: 5rem;
        }
    }
}