@use 'variables' as v;
@use 'base/mixins' as m;

h1, h2, h3 {
    font-family: v.$font_headings;
    margin: 0 0 calc(v.$separation / 2) 0 ;
    font-weight: v.$blackF;
}
h1 {
    font-size: 4.4rem;
    text-align: center;
    margin: 5rem 0 2rem;
    color: v.$darkGrey;
    @include m.tablet {
        font-size: 7.2rem;
        margin: 0 0 5rem;
        padding-top: 20rem;
    }
}
h2 {
    font-size: 4rem;
    @include m.tablet {
        font-size: 5rem;
    }
}
h3 {
    font-size: 3rem;
    @include m.tablet {
        font-size: 4rem;
    }
}
h4 {
    font-size: 2.2rem;
    
    @include m.tablet {
        font-size: 3rem;
    }
}
p {
    font-size: 1.4rem;
    color: v.$grey;
    line-height: 1.8;
    @include m.tablet {
        font-size: 2rem;
    }
}

h3, h4, p, li{
    color: v.$lightGrey;
}

.tagline {
    font-family: v.$font_headings;
    font-weight: v.$bold;
    color: v.$black;
    font-size: 2rem;
    text-align: center;
    margin-bottom: 6rem;
    margin-top: 0;
    
    @include m.tablet {
        font-size: 3rem;
        
    };
}

.remove{
    display: none;
}

.page_description{
    text-align: center;
    margin: 0;
    font-size: 2.2rem;
    color: v.$grey;
    font-weight: v.$regular;

    @include m.tablet {
        
    };

}