@use 'base/variables' as v;
@use 'base/mixins' as m;


h3{
    margin-top: 20rem;
    text-transform: uppercase;
    margin-bottom: 5rem;
}
.hero{
    background-image: linear-gradient(rgb(0, 0, 0), rgba(32, 32, 32, 0.4)),
    url(../../build/img/bgimg.png);
    background-size: cover;
    background-position: center center;
    height: 100vh;
    max-width: 100%;
    align-content: end;
    
    .hero_content{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        h1 {
            text-align: start;
            padding: 0 2rem;
            font-size: 8rem;
            @include m.tablet {
                font-size: 12rem;
                
            }
            color: v.$white;
            font-weight: v.$bold;
            max-width: 100%;
            text-transform: uppercase;
            display: flex;
            gap: 1rem;
        }
    }
    .hero_content h1 {
        white-space: nowrap; // Evita que las palabras se dividan en múltiples líneas
        overflow: hidden; // Oculta el desbordamiento de las palabras
        span {
            gap: 1rem;
            display: inline-block;
            animation: moveLeft 30s linear infinite; // Aplica la animación a cada palabra

        }
    }
    
    @keyframes moveLeft {
        0% { transform: translateX(4%); } // Inicia desde la derecha
        100% { transform: translateX(-100%); } // Se desplaza hacia la izquierda
        
        @include m.tablet {
            0% { transform: translateX(10%); } // Inicia desde la derecha
            100% { transform: translateX(-100%); } // Se desplaza hacia la izquierda
        }
    }
    
    
}

