@use 'variables' as v;
@use 'base/mixins' as m;

*, *:before, *:after {
    box-sizing: inherit;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    font-family: v.$font_texts;
    font-size: 1.6rem;
    min-height: 100%;
}
.container_background{
    background: v.$bluegradient;
}
.container {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
.container-sm {
    max-width: 35rem;
    margin: 0 auto;

    @include m.tablet {
        max-width: 50rem;
    };

    @include m.desktop {
        max-width: 90rem;
    };
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
